var enquiry = {
    validate: {
        email: function(em) {
            do_ajax({
                email: em.value,
                action: "email"
            }, "/validate", function(resp) {
                if (resp.length == 0) {
                    em.style = "background:red";
                } else {
                    em.style = "background:initial";
                }
            })
        }
    },
    submit: function(e) {
        var frm = e.form;
        var data = {
            name: $(frm).find("[name=name]").val(),
            email: $(frm).find("[name=email]").val(),
            phone: $(frm).find("[name=phone]").val(),
            action: "enquiry"
        }
        do_ajax(data, "/submit", function(resp) {
            console.log(resp);
        });
    }
}