$("#tile-1 .nav-tabs a").click(function() {
    var position = $(this).parent().position();
    var width = $(this).parent().width();
    $("#tile-1 .slider").css({ "left": +position.left, "width": width });
});
var actWidth = $("#tile-1 .nav-tabs").find(".active").parent("li").width();
var actPosition = $("#tile-1 .nav-tabs .active").position();
if ("left" in actPosition)
    $("#tile-1 .slider").css({ "left": +actPosition.left, "width": actWidth });

$(".mobile-triger button").click(function() {
    $(".navigation_bar").addClass("active");
});
$(".navigation_bar ul > a").click(function() {
    $(".navigation_bar").removeClass("active");
});

$("a.drop-profile").click(function() {
    $(".wishlist_and_cart ul li ul").toggle();
});

(function($) {
    $(document).ready(function() {
        $(".input-range").each(function() {
            var value = $(this).attr("data-slider-value");
            var separator = value.indexOf(",");
            if (separator !== -1) {
                value = value.split(",");
                value.forEach(function(item, i, arr) {
                    arr[i] = parseFloat(item);
                });
            } else {
                value = parseFloat(value);
            }
            $(this).slider({
                formatter: function(value) {
                    console.log(value);
                    return "$" + value;
                },
                min: parseFloat($(this).attr("data-slider-min")),
                max: parseFloat($(this).attr("data-slider-max")),
                range: $(this).attr("data-slider-range"),
                value: value,
                tooltip_split: $(this).attr("data-slider-tooltip_split"),
                tooltip: $(this).attr("data-slider-tooltip")
            });
        });
        $(".xzoom, .xzoom-gallery").xzoom({
            zoomWidth: 400,
            title: true,
            tint: "#333",
            Xoffset: 15
        });
        $(".xzoom2, .xzoom-gallery2").xzoom({
            position: "#xzoom2-id",
            tint: "#ffa200"
        });
        $(".xzoom3, .xzoom-gallery3").xzoom({
            position: "lens",
            lensShape: "circle",
            sourceClass: "xzoom-hidden"
        });
        $(".xzoom4, .xzoom-gallery4").xzoom({ tint: "#006699", Xoffset: 15 });
        $(".xzoom5, .xzoom-gallery5").xzoom({ tint: "#006699", Xoffset: 15 });

        //Integration with hammer.js
        var isTouchSupported = "ontouchstart" in window;

        if (isTouchSupported) {
            //If touch device
            $(".xzoom, .xzoom2, .xzoom3, .xzoom4, .xzoom5").each(function() {
                var xzoom = $(this).data("xzoom");
                xzoom.eventunbind();
            });

            $(".xzoom, .xzoom2, .xzoom3").each(function() {
                var xzoom = $(this).data("xzoom");
                $(this)
                    .hammer()
                    .on("tap", function(event) {
                        event.pageX = event.gesture.center.pageX;
                        event.pageY = event.gesture.center.pageY;
                        var s = 1,
                            ls;

                        xzoom.eventmove = function(element) {
                            element.hammer().on("drag", function(event) {
                                event.pageX = event.gesture.center.pageX;
                                event.pageY = event.gesture.center.pageY;
                                xzoom.movezoom(event);
                                event.gesture.preventDefault();
                            });
                        };

                        xzoom.eventleave = function(element) {
                            element.hammer().on("tap", function(event) {
                                xzoom.closezoom();
                            });
                        };
                        xzoom.openzoom(event);
                    });
            });

            $(".xzoom4").each(function() {
                var xzoom = $(this).data("xzoom");
                $(this)
                    .hammer()
                    .on("tap", function(event) {
                        event.pageX = event.gesture.center.pageX;
                        event.pageY = event.gesture.center.pageY;
                        var s = 1,
                            ls;

                        xzoom.eventmove = function(element) {
                            element.hammer().on("drag", function(event) {
                                event.pageX = event.gesture.center.pageX;
                                event.pageY = event.gesture.center.pageY;
                                xzoom.movezoom(event);
                                event.gesture.preventDefault();
                            });
                        };

                        var counter = 0;
                        xzoom.eventclick = function(element) {
                            element.hammer().on("tap", function() {
                                counter++;
                                if (counter == 1) setTimeout(openfancy, 300);
                                event.gesture.preventDefault();
                            });
                        };

                        function openfancy() {
                            if (counter == 2) {
                                xzoom.closezoom();
                                $.fancybox.open(xzoom.gallery().cgallery);
                            } else {
                                xzoom.closezoom();
                            }
                            counter = 0;
                        }
                        xzoom.openzoom(event);
                    });
            });

            $(".xzoom5").each(function() {
                var xzoom = $(this).data("xzoom");
                $(this)
                    .hammer()
                    .on("tap", function(event) {
                        event.pageX = event.gesture.center.pageX;
                        event.pageY = event.gesture.center.pageY;
                        var s = 1,
                            ls;

                        xzoom.eventmove = function(element) {
                            element.hammer().on("drag", function(event) {
                                event.pageX = event.gesture.center.pageX;
                                event.pageY = event.gesture.center.pageY;
                                xzoom.movezoom(event);
                                event.gesture.preventDefault();
                            });
                        };

                        var counter = 0;
                        xzoom.eventclick = function(element) {
                            element.hammer().on("tap", function() {
                                counter++;
                                if (counter == 1) setTimeout(openmagnific, 300);
                                event.gesture.preventDefault();
                            });
                        };

                        function openmagnific() {
                            if (counter == 2) {
                                xzoom.closezoom();
                                var gallery = xzoom.gallery().cgallery;
                                var i,
                                    images = new Array();
                                for (i in gallery) {
                                    images[i] = { src: gallery[i] };
                                }
                                $.magnificPopup.open({
                                    items: images,
                                    type: "image",
                                    gallery: { enabled: true }
                                });
                            } else {
                                xzoom.closezoom();
                            }
                            counter = 0;
                        }
                        xzoom.openzoom(event);
                    });
            });
        } else {
            //If not touch device

            //Integration with fancybox plugin
            $("#xzoom-fancy").bind("click", function(event) {
                var xzoom = $(this).data("xzoom");
                xzoom.closezoom();
                $.fancybox.open(xzoom.gallery().cgallery, {
                    padding: 0,
                    helpers: { overlay: { locked: false } }
                });
                event.preventDefault();
            });

            //Integration with magnific popup plugin
            $("#xzoom-magnific").bind("click", function(event) {
                var xzoom = $(this).data("xzoom");
                xzoom.closezoom();
                var gallery = xzoom.gallery().cgallery;
                var i,
                    images = new Array();
                for (i in gallery) {
                    images[i] = { src: gallery[i] };
                }
                $.magnificPopup.open({
                    items: images,
                    type: "image",
                    gallery: { enabled: true }
                });
                event.preventDefault();
            });
        }
    });
})(jQuery);

