var cart = {
    add: function(e) {
        var frm = $(e.form);
        var data = {
            product: frm.find("[name=product]").val(),
            action: 'cart-add',
            size: frm.find("[name=size]").val(),
            quant: frm.find("[name=quant]").val()
        };
        do_ajax(data, "/api/cart/", function(e) {
            location.href = "/cart";
        });
    },
    remove: function(e) {
        var frm = $(e.form);
        var data = {
            product: frm.find("[name=product]").val(),
            size: frm.find("[name=size]").val(),
            action: "cart-delete"
        }
        do_ajax(data, "/api/cart/", function(r) {
            location.reload();
            console.log(r)
        });
    },
    update: function(e) {
        var frm = $(e.form);
        var data = {
            product: frm.find("[name=product]").val(),
            size: frm.find("[name=size]").val(),
            quant: frm.find("[name=quant]").val(),
            action: "cart-update"
        }
        do_ajax(data, "/api/cart/", function(r) {
            location.reload();
            console.log(r)
        });
    },
    moveToWishlist: function(e) {
        var frm = $(e.form);
        var data = {
            product: frm.find("[name=product]").val(),
            size: frm.find("[name=size]").val(),
            action: "cart-moveToWishlist"
        }
        do_ajax(data, "/api/cart/", function(r) {
            location.reload();
            console.log(r);
        });
    }
};